body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.custom-header{
	margin-bottom: 0px;
	height: 50px;
	
}
.display-center{
	text-align: center;
}

.custom-dashboard{
	/* margin-top: 25px; */
	max-width: 900px;
	margin: 25px auto;
}
.display-mid p{
	font-size: small;
	margin-bottom: 2px;
}
.custom-panel{
	max-height: 20vh;
	padding: 1px;
	box-shadow: 0px 4px 8px -6px black; 

}
.custom-panel-body{
	text-align: center;
	padding: 2px;
	max-height: inherit;
}
p{
	font-size: xx-small;
	padding: 2px;
	margin: 0;
}
h5{
	margin: 0;
}
.wrapper {
	/* display: flex; */
	/* align-items: center; */
	margin: auto;
	position: absolute;
	width: calc(100vw - 50px);
	height: calc(100vh - 50px);
	top: 50px;
	left: 50px;
	background-color: rgb(250,250,250);
}

#sidebar {
	width: 50px;
	height: calc(100vh - 50px);
	position: fixed;
	top: 50px;
	z-index: 999;
}
.spacing{
	padding: 4px;
	margin-top: 8px;	
	margin-bottom: 8px;
	margin-right: auto;
	margin-left: 4px;
}
